import Vue from 'vue'
import App from './App'
import {router} from './router'
import {store} from './store/store'
import axios from 'axios'

window.axios = axios;

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



